<template>
	<div> 
	<ul>
		<li v-for="p in news" :key="p.id">
			<!-- <router-link :to="`/home/news/detail?id=${p.id}&name=${p.name}`">{{p.id}}-{{p.name}}</router-link> -->
			<router-link
					:to="{
						path:'/home/news/detail',
						query:{
							id:p.id ,
							name:p.name
						}
					}"
			>{{p.id}}-{{p.name}}</router-link>
			<button @click="pushShow(p)">push查看</button>

		</li>
		
	</ul>
	
	<hr>
	<router-view></router-view>

</div>
    
</template>

<script>
export default {
    name:'News',
	data(){
		return{
			news:[{id:'001',name:'wzm'},{id:'002',name:'lzy'},{id:'003',name:'hj'}]
		}
	},
	methods:{
		pushShow(p){
			this.$router.push({
				path:'/home/news/detail',
						query:{
							id:p.id ,
							name:p.name
						}
			})
		}
	}
}
</script>

